import { NextRequest, NextResponse } from 'next/server';
import connectToDatabase from '@/lib/db';
import Guestbook from '@/models/Guestbook';
import { getServerSession } from 'next-auth';
import { authOptions } from '@/lib/auth';

export async function GET(req: NextRequest) {
    try {
        const { searchParams } = new URL(req.url);
        const isAdmin = searchParams.get('admin') === 'true';

        await connectToDatabase();

        if (isAdmin) {
            const session = await getServerSession(authOptions);
            if (session) {
                const messages = await Guestbook.find({}).sort({ createdAt: -1 });
                return NextResponse.json(messages);
            }
        }

        // Public only sees approved messages
        const messages = await Guestbook.find({ approved: true }).sort({ createdAt: -1 }).limit(100);
        return NextResponse.json(messages);
    } catch (error) {
        return NextResponse.json({ error: 'Failed to fetch messages' }, { status: 500 });
    }
}

export async function POST(req: NextRequest) {
    try {
        const body = await req.json();

        if (!body.name || !body.message) {
            return NextResponse.json({ error: 'Name and message are required' }, { status: 400 });
        }

        await connectToDatabase();

        const newMessage = await Guestbook.create({
            name: body.name,
            message: body.message,
            approved: false // Requires admin approval
        });

        return NextResponse.json(newMessage, { status: 201 });
    } catch (error) {
        return NextResponse.json({ error: 'Failed to sign guestbook' }, { status: 500 });
    }
}
